import './Animation.css'
import Getintouch from '../../../components/GetInTouch/getintouch'
import { Helmet } from 'react-helmet-async'

function Animation () {
  return (
    <>
      <Helmet>
        <title>Animations - Tech Solution, Digital Solution</title>
        <meta
          name='description'
          content='We Have All You Need For Your Animation.....
When you’re ready to take your animations to the next level, discover all that you can do with Character animations. Animation is the art of bringing life to an otherwise inanimate objects, or illustrated / 3D generated characters. 
It is created by projecting sequenced images quickly, one after another, to create the illusion of life. 

3D Animation Video
3D animations is essential for providing context and engaging your audience also give viewers an idea of what to expect with a story
'
        />
        <link rel='canonical' href='https://elonatech.com.ng/' />
      </Helmet>
      {/*===================================================================== header =======================================================*/}
      <div class='container-fluid animations-section'>
        <div class='text-content'>
          <h2>Animations</h2>
          <h5>
            Creating engaging and immersive animations is a dynamic and
            captivating way to bring your ideas to life.
          </h5>
          <p class='lead'>
            Animations have the power to convey complex concepts, evoke
            emotions, and leave a lasting impression on your audience.
          </p>
        </div>
      </div>

      {/*============================================================= VIDEO CARD ===============================================================*/}
      <div className=' container '>
        <h1 className='text-center mb-5  fw-bold'>Animations Video Template</h1>
        <div class='container  mb-5'>
          <div class='row '>
            <div class='col-md-4 mt-2 '>
              <div class='card h-100'>
                <div class='ratio ratio-4x3'>
                  <iframe
                    src='https://www.youtube.com/embed/nbMtGm-b9Kg'
                    title='5 Reasons why your Business needs Animated Videos'
                    frameborder='0'
                    allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share'
                    allowfullscreen
                  ></iframe>
                </div>
                <div class='card-body'>
                  <h4 class='card-text  fw-bold'>3D Animation Video </h4>
                  <p className='text-justify'>
                    3D animations is essential for providing context and
                    engaging your audience also give viewers an idea of what to
                    expect with a story
                  </p>
                </div>
              </div>
            </div>

            <div class='col-md-4 mt-2'>
              <div class='card h-100'>
                <div class='ratio ratio-4x3'>
                  <iframe
                    width='360'
                    height='270'
                    src='https://www.youtube.com/embed/v-XT2EWLQgA'
                    title='White board Animations'
                    frameborder='0'
                    allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share'
                    allowfullscreen
                  ></iframe>
                </div>
                <div class='card-body'>
                  <h4 class='card-text fw-bold'>Whiteboard Animation </h4>
                  <p className='text-justify'>
                    "White animation" typically refers to animations with a
                    predominant use of white or light-colored backgrounds and
                    elements.
                  </p>
                </div>
              </div>
            </div>

            <div class='col-md-4 mt-2 '>
              <div class='card h-100'>
                <div class='ratio ratio-4x3'>
                  <iframe
                    width='1600'
                    height='900'
                    src='https://www.youtube.com/embed/EmkNTtbWmic'
                    title='Digital Marketing Animation'
                    frameborder='0'
                    allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share'
                    allowfullscreen
                  ></iframe>
                </div>
                <div class='card-body'>
                  <h4 class='card-text fw-bolder'> Explainer Animation.</h4>
                  <p className='text-justify'>
                    A "Trendy Explainer Toolkit" is a versatile resource used
                    for creating modern and stylish explainer videos.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/*======================================================================== WE HAVE ALL YOUR NEEDS ==============================================================*/}
      <div
        className='container-fluid animation-context'
        style={{ backgroundColor: '#34548c' }}
      >
        <div class='row align-items-center '>
          <div class='col-md-6'>
            <img
              src='https://res.cloudinary.com/elonatech/image/upload/v1709645935/2dAnimationPage/animation_111_720_aoesan.png'
              alt=''
              style={{ marginBottom: '100px' }}
              className='mb-2 img-fluid'
            />
          </div>
          <div class='col-md-6'>
            <div class='card rounded-0 border-0' style={{ padding: '3rem' }}>
              <div class='card-body'>
                <h5 class='card-title fs-1'>
                  We Have All You Need For Your Animation.....
                </h5>
                <p class='card-text taj'>
                  When you’re ready to take your animations to the next level,
                  discover all that you can do with Character animations.
                  Animation is the art of bringing life to an otherwise
                  inanimate objects, or illustrated / 3D generated characters.
                  It is created by projecting sequenced images quickly, one
                  after another, to create the illusion of life.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/*================================================================== CUSTOMERS SAY =========================================================*/}
      <div class='p-3 mb-2  text-white' style={{ background: '#F5FFFA' }}>
        <div className='container mt-4 mb-4'>
          <h2
            className='text-center text-black mb-4 fw-bold'
            data-aos='fade-right'
            data-aos-duration='2000'
          >
            {' '}
            OUR CUSTOMERS SAY
          </h2>
          <div class='row'>
            <div
              class='col-sm-4 mb-4 p-2'
              data-aos='fade-up'
              data-aos-anchor-placement='top-center'
              data-aos-duration='1000'
            >
              <div class='card h-100'>
                <div class='card-body'>
                  <h5 class='card-title text-center'>Alan D.</h5>
                  <p class='card-text taj'>
                    The animation was delivered on time and within budget, which
                    is always a plus in the creative industry. Our clients were
                    thrilled with the end product, and it has had a significant
                    impact on our marketing efforts.
                  </p>
                </div>
              </div>
            </div>
            <div
              class='col-sm-4 mb-4 p-2'
              data-aos='fade-up'
              data-aos-anchor-placement='top-center'
              data-aos-duration='2000'
            >
              <div class='card h-100'>
                <div class='card-body'>
                  <h5 class='card-title text-center'>Hasan H.</h5>
                  <p class='card-text taj'>
                    I highly recommend elonatech for any 3D animation project.
                    Their talent, dedication, and commitment to excellence are
                    truly remarkable. I look forward to working with them again
                    in the future and bringing more of our creative ideas to
                    life.
                  </p>
                </div>
              </div>
            </div>
            <div
              class='col-sm-4 mb-4 p-2'
              data-aos='fade-up'
              data-aos-anchor-placement='top-center'
              data-aos-duration='3000'
            >
              <div class='card h-100'>
                <div class='card-body'>
                  <h5 class='card-title text-center'>John D</h5>
                  <p class='card-text taj'>
                    "Working with Elonatech on our recent animation project was
                    an absolute pleasure. They took our vision and brought it to
                    life with incredible creativity and attention to detail. The
                    animation exceeded our expectations, and our audience loved
                    it. We're thrilled with the results!"
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* ================================================================= */}
      <div className='row g-0'>
        <div className='col-lg-6'>
          <div className='card border-0'>
            <img
              src='https://res.cloudinary.com/elonatech/image/upload/v1709645939/2dAnimationPage/animation_jeperf.jpg'
              alt='img-fluid lazyload rounded'
              style={{ height: '35rem' }}
            />
          </div>
        </div>
        <div className='col-lg-6'>
          <div className=' text-black align-items-center faq'>
            <h2 class='font-weight-bold heading pt-3 ps-5 fw-bold'>FAQs</h2>
            <p class='text-black fs-6 ' style={{ textAlign: 'justify' }}>
              <ul className='list-unstyled px-5'>
                <li className='pb-2'>
                  {' '}
                  <span className='fw-bold fs-5' style={{ color: 'black' }}>
                    What types of animation services do you offer?
                  </span>
                  : We offer a wide range of animation services, including 2D
                  animation, 3D animation, motion graphics, character animation,
                  and more. We cater to various industries and project
                  requirements.
                </li>
                <li className='pb-2'>
                  {' '}
                  <span className='fw-bold fs-5' style={{ color: 'black' }}>
                    How much does animation production cost?:
                  </span>{' '}
                  The cost of animation varies depending on the complexity of
                  the project, the duration, and specific client requirements.
                  Please contact us for a customized quote.
                </li>
                <li className='pb-2'>
                  {' '}
                  <span className='fw-bold fs-5' style={{ color: 'black' }}>
                    What is the typical timeline for an animation project?:
                  </span>{' '}
                  Project timelines can vary significantly. Small animations may
                  take a few weeks, while more complex projects may require
                  several months. We'll provide you with a detailed timeline
                  during the project planning phase.
                </li>
                <li className='pb-2'>
                  {' '}
                  <span className='fw-bold fs-5' style={{ color: 'black' }}>
                    Do you work with international clients?:
                  </span>{' '}
                  Absolutely! We work with clients from all over the world. Our
                  team can communicate effectively in English and other
                  languages, ensuring a smooth collaboration.
                </li>
                <li className='pb-2'>
                  {' '}
                  <span className='fw-bold fs-5' style={{ color: 'black' }}>
                    What is the process for creating an animation with your
                    studio?:
                  </span>{' '}
                  Our animation production process includes concept development,
                  scriptwriting, storyboarding, design, animation, sound design,
                  and final revisions. We collaborate closely with clients
                  throughout each step.
                </li>
              </ul>
            </p>
          </div>
        </div>
      </div>

      {/* ============================================================================== */}
      <Getintouch />
      {/* ============================================================================== */}
    </>
  )
}

export default Animation